.home {
    h3 {
        color: #fb4275;
        font-weight: 500;
    }

    .stage-1 {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 1rem;
    }

    .stage-1 .stage-1-left {
        display: flex;
        flex-direction: column;
        align-content: flex-start;
        flex-wrap: nowrap;
        flex: 6 1 0;
        color: #32196d;
        font-size: 24px;
        gap: 1.5rem;

        h1 {
            margin: 0 !important;
            padding: 0 !important;
        }

        p {
            font-family: 'Muli, sans-serif';
            color: #818181;
            text-align: left;
            font-weight: 400;
            font-size: 18px;
            line-height: 1.67;
            max-width: 80%;
        }
    }

    .stage-1 .stage-1-right {
        display: flex;
        flex: 3 1 0;
    }


    .stage-2 {
        padding-top: 4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        h2 {
            color: #3f3f3f;
            font-size: 30px;
        }

        span {
            font-family: 'Muli, sans-serif' !important;
            color: #818181;
            text-align: left;
            font-weight: 400;
            font-size: 18px;
            max-width: 80%;
            line-height: 1.2
        }
    }

    .stage-3 {
        padding-top: 4rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        h2 {
            color: #3f3f3f;
            font-size: 30px;
        }

        span {
            font-family: 'Muli, sans-serif' !important;
            color: #818181;
            text-align: left;
            font-weight: 400;
            font-size: 18px;
            max-width: 80%;
            line-height: 1.2
        }

        ul {
            list-style-type: none;
            padding: 0;
            margin-top: 14px !important;
            line-height: 1.2;
            font-weight: 400;
            font-size: 20px;
            color: #818181;
            font-family: 'Muli, sans-serif' !important;

            img {
                width: 18px;
                height: auto;
                padding-right: 8px;
            }
        }

        li {
            text-align: left;
            margin: 15px 0;
        }

        .sector {
            display: flex;
            justify-content: flex-start;
            flex-direction: column;
            place-self: center;
        }
    }

    .stage-4 {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;

        .sector {
            text-align: left;
        }

        h2 {
            color: #3f3f3f;
            font-size: 30px;
        }

        ul {
            list-style-type: none;
            padding: 0;
            margin-top: 14px !important;
            line-height: 1.2;
            font-weight: 400;
            font-size: 20px;
            color: #818181;
            font-family: 'Muli, sans-serif' !important;

            img {
                width: 18px;
                height: auto;
                padding-right: 8px;
            }
        }

        li {
            text-align: left;
            margin: 15px 0;
        }

    }

    @media (max-width: 768px) {
        .stage-1 {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;



            h1 {
                text-align: left;
            }
        }

        .stage-1-left,
        .stage-1-right {
            flex-wrap: unset !important;
            flex: unset !important;
            gap: 0 !important;
            width: 100%;
            p {
                max-width: 100% !important;
            }
        }

        .stage-1-right img {
            width: 100%;
            height: auto;
        }
    }

}

.MuiBox-root {
    padding: 24px 0 !important;
}